:root {
  --pisang: #ffff4e;
  --pan: #ff9b00;
  --pink-otak: #e36671;
  --bgc-primary: #f7f7f7;
  --text-primary: #4d4b45;
  --pisang-biru: #4ea7ff;
  --pisang-merah: #ff4e4f;
}

body {
  background-color: var(--bgc-primary);
  color: var(--text-primary);
}

.navbar {
  background-color: var(--pisang);
}

.navbar .logo-title {
  /* img logo
  height: 24px;
  padding: 0;
  margin: 0;
  width: auto; */
  color: var(--pan);
  font-family: "Helvetica Neue", "Helvetica", "Arial", "sans-serif";
  /* font-family: Tesla; */
}

.home {
  /* width: 100vw; */
  /* if uncomment above, side scrolling appear */
  height: 75vh;
  display: flex;
  /* justify-content: center; */
  /* align-items: center; */
  margin-top: 107px;
}

.home .logo {
  max-width: 500px;
  position: absolute;
  z-index: -5;
  opacity: 0.5;
  transform: translate(40px, 200px) rotate(-20deg);
}

.home .title {
  /* z-index: 5; */
  text-align: left;
  color: aliceblue;
  text-shadow: 3px 3px grey;
}

.home .title .transparent {
  color: transparent;
  text-shadow: 3px 3px transparent;
}

.main-pages {
  padding-top: 20px;
  padding-bottom: 20px;
}

.main-pages .title {
  text-align: center;
  /* height: 10vh; */
  /* padding: 14px 0; */
}

.in-construction {
  max-width: 400px;
  height: 80vh;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.in-construction .icon {
  font-size: 6em;
}

/* About Us */
#about-us {
  background-color: var(--pisang-biru);
  color: azure;
}

/* Projects */
#projects-list {
  background-color: #a74eff;
  color: azure;
  text-align: center;
}

/* Contact Us */
#contact-us i {
  font-size: 1.3em;
}

#contact-us .name {
  font-weight: 450;
  font-size: 1.1em;
}

#contact-us .location {
  font-size: 0.8em;
}

/* Services */
#services {
  background-color: var(--pisang-merah);
  color: var(--bgc-primary);
}

/* footer */
.page-footer {
  background-color: var(--text-primary);
  color: var(--bgc-primary);
}

.page-footer .icon-container {
  width: 80vw;
  margin: auto;
  padding: 30px 0;
  font-size: 2em;
  display: flex;
  justify-content: space-around;
}

.page-footer a {
  color: var(--bgc-primary);
  padding: 0;
  margin: 0;
}

.page-footer a:hover {
  color: var(--pisang);
}

